@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
.Home {
    text-align: center;
    height: 100vh;
}
h1{
    margin: 0;
}


.List {
    font-family: sans-serif;
    text-align: center;
}

.animes-list {
    display: grid;
    grid-template-columns: repeat(
        auto-fit,
        minmax(200px, 1fr)
    );
    list-style: none;
    grid-gap: .1rem;
}

.animes-list img {
    max-width: 100%;
}

.card-serie{
    background-color: #185ADB;
    max-width: 180px;
    max-height: 310px;
    border-radius: 10px;
    margin: 15px;
    font-family: 'Roboto', sans-serif;
    transition: all ease 0.2s;
}

.card-serie:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.poster-image {
    width: 100%;
    border-radius: 10px 10px 0 0;
    background-size: 230px;
    background-repeat: no-repeat; 
}
.fav-btn {
    position: absolute;
    margin: 5px;
    color: #EFEFEF;
}

.fav-btn:hover {
    color: #FFC947;
}

.card-datail{
    text-align: left;
    margin-left: 10px;
    color: #EFEFEF;
    height: 80px;
    overflow: hidden;
    text-overflow: Ellipsis;
}
.card-datail h1 {
    font-size: 100%;
    padding: 0;
    margin: 0;
}

.card-datail h2{
    font-size: .8em;
    opacity: .7;
    font-weight: lighter;
}
.detail{
    text-decoration: none;
}
.calendar__box{
    background-color:#ffffff ;
    margin: 50px;
    border: solid 10px #ffffff;
}

.Home {
    text-align: center;
}


.anime--page{
    height: 100vh;
}

.info {
    display: flex;
    position: relative;
    z-index: 5;
}

.info .info--bg {
    background: no-repeat;
    background-size: cover;
    position: absolute; 
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -1;
    opacity: .2;
    box-shadow: inset 0px -100px 100px #0A1931;
}
.info--button__back {
    background-color: transparent;
    border: 2px solid #c4c4c4;
    color: #fff;
    border-radius: 10px;
    display: flex;
    align-items: center;
    cursor: pointer;
    align-self: flex-start;
    margin: 15px;
}

.info img {
    border-radius: 15px;
    
    max-height: 350px;
    margin: 15px;
}

.info--anime {
    color: #c4c4c4;
    margin-left: 20px;
}

.info--anime h1 {
    color: #fff;
}
.info--anime p {
    width: 70%;
}
.info--anime span {
    color: #FFC947;
}

.info--btn-fav {
    border: none;
    padding: 10px;
    text-decoration: none;
    cursor: pointer;
    width: 70%;
    transition: background .3s;
    border-radius: 5px;
    background-color: #185ADB;
}

.info--btn-fav:hover{
    background-color: #FFC947;
}
.info--box {
    text-align: center;
}
.Favorites {
    text-align: center;
    height: 100vh;
}
h1{
    margin: 0;
}


.list--card {
    flex-direction: column;
    background-color: #185ADB;
    width: 80%;
    margin-left: 100px;
    padding-bottom: 30px;
    padding-right: 30px;
    padding-top: 30px;
    border-radius: 10px;
    list-style: none;
}


.list--card h1 {
    font-family: 'Anton', sans-serif;
    color: #FFC947;
    letter-spacing: .2rem;
}

.card {
    display: flex;
    border: 2px solid #FFC947;
    margin-left: 30px;
}

.card img {
    max-width: 100px;
    max-height: 150px;
}

.card--info {
    color: #EFEFEF;
    margin-left: 10px;
    display: flex;
    width: 100%;
}
.card button{
    padding: 10px 20px 10px 20px;
    max-height: 50px;
    margin: 2% 2% 0 0;
    background-color: #FFC947;
    border: none;
    border-radius: 10px;
    color: #0A1931;
    transition: all ease 0.2s;
}

.card button:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    background-color: #ff0000;
    color: #ffffff;
    cursor: pointer;
}


.NavbarItems {
    background: linear-gradient(90deg, #2662db 0%, #185ADB 100%);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
}

.navbar-logo{
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
}

.fa-react{
    margin-left: 0.5rem;
    font-size: 1.6rem;
}

.nav-menu{
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 10px;
    gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right: 2rem;
}

.nav-links {
    color: #fff;
    text-decoration: none;
    padding: 0.5rem 1rem;
}

.nav-links:hover {
    background-color: #0c42af;
    border-radius: 4px;
    transition: all 0.2s ease-out;
}

.fa-bars {
    color: #fff;
}

.nav-links-mobile{
    display:none;
}

.menu-icon {
    display: none;
}

@media screen and (max-width: 960px) {
    .NavbarItems {
        position: relative;
    }

    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 500px;
        position:absolute;
        top: 60px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .nav-menu.active {
        background: #0c42af;
        left: -4.3%;
        opacity: 1;
        transition: all 0.5s ease;
        z-index:10;
    }

    .nav-links {
        text-align:center;
        padding: 1.74rem;
        width: 100%;
        display: table;
    }
    .nav-links:hover{
        background-color: #0a2f7a;
        border-radius: 0;
    }

    .navbar-logo {
        position: absolute;
        top: 0;
        left: 0;
        -webkit-transform: translate(25%, 50%);
                transform: translate(25%, 50%);
    }

    .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        -webkit-transform: translate(-100%, 60%);
                transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
    }

    .fa-times {
        color: #fff;
        font-size: 2rem;
    }

    .nav-links-mobile{
        display: block;
        text-align: center;
        padding: 1.5rem;
        margin: 2rem auto;
        border-radius: 4px;
        width: 80%;
        background: #2662db;
        text-decoration: none;
        color: #fff;
        font-size: 1.5rem;
    }

    .nav-links-mobile:hover {
        background: #fff;
        color: #185ADB;
        transition: 250ms;
    }

    Button {
        display: none;
    }
}
