#clearBtn {
    cursor: pointer;
}
:root {
    --primary: #4478df;
}

.btn {
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn:hover {
    padding: 8px 20px;
    transition: all 0.3s ease-out;
    background: #fff;
    color: #4d85f5;
    transition: 250ms;
} 

.btn--primary {
    background-color: #4478df;
    background-color: var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border-radius: 4px;
    border: 1px solid #4478df;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium {
    padding: 8px 20px;
    border-radius: 4px;
    font-size: 18px;
    color: #fff;
}

.btn--large{
    padding: 12px 26px;
    border-radius: 4px;
    font-size: 20px;
    color: #fff;
}


html, body {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
}

.App {
    background-color: #0A1931;
    font-family: 'Montserrat', sans-serif;
}   
